.our-clients {
	height: 935px;
	position: relative;
	@media(max-width: 790px) {
		height: 775px; }
	@media(max-width: 600px) {
		height: 615px; }
	& .our-clients__circles {
		position: absolute;
		top: -508px;
		left: -370px;
		@media(max-width: 1200px) {
			width: 1400px; }
		@media(max-width: 790px) {
			width: 1168px; }
		@media(max-width: 600px) {
			display: none; }
		& .circles__img {
			width: 100%; } }
	& .our-clients__circles--375 {
		display: none;
		@media(max-width: 600px) {
			display: block;
			width: 680px;
			height: 528px;
			position: absolute;
			top: -104px;
			left: -135px; }
		@media(max-width: 400px) {
			width: 528px;
			left: -118px; }
		& .circles--375__img {
			width: 100%; } }
	& .our-clients__fix-container {
		& .our-clients__text-block {
			margin: 245px 0 0 0;
			width: 610px;
			height: 270px;
			@media(max-width: 1200px) {
				margin: 180px 0 0 0; }
			@media(max-width: 790px) {
				width: 435px;
				margin: 130px 0 0 0; }
			@media(max-width: 600px) {
				padding: 70px 0 0 0;
				margin: 35px 0 0 0;
				width: 100%; }
			@media(max-width: 500px) {
				display: flex;
				flex-direction: column;
				align-items: center;
				text-align: center; }
			@media(max-width: 400px) {
				padding: 0;
				width: 95%;
				display: block;
				text-align: left; }
			& h1 {
				margin: 0 0 88px 0;
				font-size: 44px;
				font-weight: 400;
				text-transform: uppercase;
				@media(max-width: 1180px) {
					font-size: 36px; }
				@media(max-width: 790px) {
					font-size: 27px;
					margin: 0 0 30px 0; }
				@media(max-width: 600px) {
					margin: 0 0 26px 0;
					text-align: left;
					font-size: 15px; }
				@media(max-width: 400px) {
					text-align: center; } }
			& span {
				font-size: 27px;
				line-height: 1.5;
				letter-spacing: 1px;
				@media(max-width: 1180px) {
					font-size: 22px; }
				@media(max-width: 790px) {
					font-size: 14px; }
				@media(max-width: 600px) {
					font-size: 13px;
					line-height: normal;
					letter-spacing: normal; } } }
		& .our-clients__blot {
			position: absolute;
			&:hover {
				transform: scale(1.1); }
			@media(max-width: 1200px) {
				width: 85px; }
			@media(max-width: 600px) {
				width: 43px; }
			& .animate-cloud {
				position: relative;
				@media(max-width: 1200px) {
					width: 100%;
					height: 100%; }
				& img {
					width: 100%; }
				& .mask {
					width: 1px;
					height: 1px;
					position: absolute;
					top: 50%;
					left: 50%;
					z-index: -2;
					border-radius: 50%; } }
			& .our-clients__position-container {
				position: relative;
				display: none;
				& .popup {
					position: absolute;
					top: 20px;
					width: 295px;
					height: 113px;
					background-color: #FFF6DD;
					border-radius: 10px;
					display: flex;
					justify-content: space-between;
					align-items: center;
					@media(max-width: 925px) {
						left: -30px; }
					@media(max-width: 790px) {
						width: 270px;
						height: 100px; }
					@media(max-width: 600px) {
						width: 195px;
						height: 85px; }
					& img {
						max-height: 100%; }
					& .popup__text {
						width: 205px;
						font-size: 18px;
						line-height: 22px;
						padding: 0 0 0 14px;
						@media(max-width: 790px) {
							font-size: 11px; }
						@media(max-width: 600px) {
							font-size: 11px;
							line-height: normal; } } } } }
		& .blot1 {
			top: (37700%/935);
			left: 100px;
			z-index: 4;
			@media(max-width: 1200px) {
				top: 36.32086%; }
			@media(max-width: 790px) {
				top: 27.32086%;
				left: 50px; }
			@media(max-width: 600px) {
				top: 45.32086%; }
			@media(max-width: 400px) {
				top: 31.32086%;
				left: 29px; } }
		& .animate--pulse1 {
			animation: pulse 2s linear infinite;
			animation-delay: 0s;
			@media(max-width: 1180px) {
				top: 36.32086%; }
			@media(max-width: 790px) {
				top: 29.32086%; }
			@media(max-width: 600px) {
				top: 208+77px;
				left: 58px; }
			@media(max-width: 400px) {
				top: 208px; } }
		& .blot2 {
			top: 701+18px;
			left: 320px;
			@media(max-width: 1200px) {
				top: 600px;
				left: 157px; }
			@media(max-width: 790px) {
				top: 413px;
				left: 59px; }
			@media(max-width: 400px) {
				top: 300px;
				left: 52px; } }
		& .animate--pulse2 {
			animation: pulse 2s linear infinite;
			animation-delay: 0s;
			@media(max-width: 1180px) {
				top: 593px;
				left: 181px; }
			@media(max-width: 790px) {
				top: 427px;
				left: 138px; }
			@media(max-width: 600px) {
				top: 422px;
				left: 93px; }
			@media(max-width: 400px) {
				top: 313px; } }
		& .blot3 {
			top: 505+18px;
			left: 613px;
			z-index: 3;
			@media(max-width: 1200px) {
				top: 479px;
				left: 376px; }
			@media(max-width: 790px) {
				top: 322px;
				left: 242px; }
			@media(max-width: 600px) {
				top: 374px;
				left: 188px; }
			@media(max-width: 400px) {
				top: 263px;
				left: 138px; }
			& .our-clients__position-container {
				& .popup {
					height: 154px;
					@media(max-width: 790px) {
						height: 100px; }
					@media(max-width: 600px) {
						height: 100px; }
					& img {
						padding: 0 12px 0 0; } } } }
		& .animate--pulse3 {
			animation: pulse 2s linear infinite;
			animation-delay: 0s;
			@media(max-width: 1180px) {
				top: 471px;
				left: 372px; }
			@media(max-width: 790px) {
				top: 309px;
				left: 290px; }
			@media(max-width: 600px) {
				top: 376px;
				left: 167px; }
			@media(max-width: 400px) {
				top: 261px; } }
		& .blot4 {
			top: 402+18px;
			left: 961px;
			@media(max-width: 1200px) {
				top: 536px;
				left: 600px; }
			@media(max-width: 790px) {
				top: 397px;
				left: 375px; }
			@media(max-width: 600px) {
				top: 411px;
				left: 311px; }
			@media(max-width: 400px) {
				top: 275px;
				left: 260px; }
			& .our-clients__position-container {
				& .popup {
					height: 144px;
					@media(max-width: 890px) {
						left: -120px; }
					@media(max-width: 790px) {
						height: 100px; }
					@media(max-width: 600px) {
						height: 100px; }
					@media(max-width: 400px) {
						left: -100px; }
					& img {
						padding: 0 12px 0 0; } } } }
		& .animate--pulse4 {
			animation: pulse 2s linear infinite;
			animation-delay: 0s;
			@media(max-width: 1180px) {
				top: 545px;
				left: 561px; }
			@media(max-width: 790px) {
				top: 375px;
				left: 429px; }
			@media(max-width: 600px) {
				top: 430px;
				left: 249px; }
			@media(max-width: 400px) {
				top: 283px; }
			@media(max-width: 300px) {
				top: 283px; } }
		& .blot5 {
			top: 102+18px;
			left: 949px;
			z-index: 4;
			@media(max-width: 1200px) {
				top: 278px;
				left: 579px; }
			@media(max-width: 790px) {
				top: 184px;
				left: 385px; }
			@media(max-width: 600px) {
				top: 281px;
				left: 316px; }
			@media(max-width: 400px) {
				top: 171px;
				left: 250px; }
			& .our-clients__position-container {
				& .popup {
					height: 137px;
					@media(max-width: 890px) {
						left: -120px; }
					@media(max-width: 790px) {
						height: 100px; }
					@media(max-width: 600px) {
						height: 100px; }
					@media(max-width: 400px) {
						left: -100px; }
					& img {
						padding: 0 12px 0 0; } } } }
		& .animate--pulse5 {
			animation: pulse 2s linear infinite;
			animation-delay: 0s;
			@media(max-width: 1180px) {
				top: 268px;
				left: 578px; }
			@media(max-width: 790px) {
				top: 159px;
				left: 416px; }
			@media(max-width: 600px) {
				top: 302px;
				left: 277px; }
			@media(max-width: 400px) {
				top: 143px; } }
		& .our-clients__cloud {
			position: absolute;
			bottom: (-22700%/935);
			right: (-31500%/1440);
			z-index: -1;
			@media(max-width: 1180px) {
				width: 447px; }
			@media(max-width: 910px) {
				width: 300px;
				bottom: -9.27807%;
				right: -18.875%; }
			@media(max-width: 650px) {
				width: 256px; }
			@media(max-width: 590px) {
				width: 164px;
				height: 175px;
				bottom: 12px;
				right: -52px; }
			& .cloud__img {
				width: 100%;
				animation: cloud ease-in-out infinite;
				animation-duration: 5s; } } } }
